.button {
  display: block;
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/tildaSansLight.woff2") format("woff2"), url("../fonts/tildaSansLight.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/tildaSansRegular.woff2") format("woff2"), url("../fonts/tildaSansRegular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/tildaSansMedium.woff2") format("woff2"), url("../fonts/tildaSansMedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/tildaSansBold.woff2") format("woff2"), url("../fonts/tildaSansBold.woff") format("woff");
  font-display: swap;
}
.link {
  text-decoration: none;
  margin: 0;
  color: #474242;
  outline: 2px solid transparent;
  border-radius: 7px;
  cursor: pointer;
}
.link:hover {
  filter: drop-shadow(2px 2px 10px #e98b3f);
}
.link:focus {
  outline-color: #e98b3f;
}
.link:active {
  color: #e98b3f;
  outline-color: transparent;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.text {
  margin: 0 0 10px;
}
.text--experience {
  margin: 0;
}

.title {
  font-size: 17px;
  font-weight: 400;
  color: #474242;
  text-indent: initial;
  margin: 0 0 20px;
}
.title--info {
  margin: 20px 1rem;
  filter: drop-shadow(2px 2px 10px #474242);
}
@media (min-width: 768px) {
  .title {
    margin: 0 0 40px;
  }
  .title--info {
    margin: 30px 0 30px;
  }
  .title--info-dates {
    margin: 0 0 20px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  margin: 0 19px;
}
@media (min-width: 768px) {
  .wrapper {
    margin: 0 40px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    margin: 0 60px;
  }
}

.page {
  height: 100%;
  font-family: "Tilda Sans", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: normal;
}

.page__body {
  margin: 0;
}

.body {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

@media (min-width: 1440px) {
  .body__header {
    margin: 0 auto;
  }
}

.body__up-button {
  display: none;
  position: fixed;
  z-index: 1;
  bottom: 145px;
  right: 24px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #e98b3f;
  background-color: transparent;
  box-shadow: 2px 2px 10px #e98b3f;
  cursor: pointer;
}
.body__up-button--show {
  display: block;
}
@media (min-width: 768px) {
  .body__up-button {
    right: 2px;
  }
}
@media (min-width: 1440px) {
  .body__up-button {
    right: 10px;
  }
}

.header {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100vh;
  margin-bottom: 20px;
}
.header--info {
  height: 100%;
}

.header__container {
  width: 100%;
  height: 100%;
  background: url("../image/decorative-images/index-background-mobile.png") no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-resolution: 2dppx) {
  .header__container {
    background: url("../image/decorative-images/index-background-mobile@2x.png") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (min-width: 768px) {
  .header__container {
    background: url("../image/decorative-images/index-background-tablet.png") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .header__container {
    background: url("../image/decorative-images/index-background-tablet@2x.png") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (min-width: 1440px) {
  .header__container {
    background: url("../image/decorative-images/index-background-desktop.png") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .header__container {
    background: url("../image/decorative-images/index-background-desktop@2x.png") no-repeat;
    background-size: cover;
    background-position: center;
  }
}

.header__button {
  padding: 5px;
  transition: transform 0.7s;
  filter: drop-shadow(2px 2px 10px #808080);
}
.header__button--transform {
  transform: translateY(-15px);
}

.header__button-arrow {
  fill: #474242;
}

.nav {
  width: 100%;
}

@media (min-width: 1440px) {
  .nav__list {
    margin: 0 auto;
  }
}

.nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  flex-wrap: wrap;
  padding: 35px 20px;
}
@media (min-width: 768px) {
  .nav-list {
    flex-direction: row;
    gap: 20px 39.5px;
    padding: 50px 40px;
  }
}
@media (min-width: 1440px) {
  .nav-list {
    gap: 20px 197.5px;
    max-width: 1320px;
    padding: 50px 60px;
  }
}

.nav-list__item {
  text-align: center;
  margin: 0;
}

.nav-list__link {
  display: block;
  font-size: 20px;
  padding: 5px;
  color: #474242;
  font-weight: 400;
  filter: drop-shadow(2px 2px 10px #808080);
}
@media (min-width: 768px) {
  .nav-list__link {
    font-size: 21px;
  }
}
@media (min-width: 1440px) {
  .nav-list__link {
    font-size: 22px;
  }
}

.main {
  flex-grow: 1;
}

.main__portfolio {
  margin-bottom: 40px;
}
@media (min-width: 1440px) {
  .main__portfolio {
    margin: 0 auto 40px;
  }
}

@media (min-width: 1440px) {
  .portfolio {
    max-width: 1320px;
  }
}

.portfolio__container {
  padding-top: 20px;
}
@media (min-width: 768px) {
  .portfolio__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 40px;
    margin-bottom: 50px;
  }
}

.portfolio__title {
  font-size: 30px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .portfolio__title {
    margin-bottom: 0;
  }
}

.portfolio__sorting-type {
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .portfolio__sorting-type {
    margin-bottom: 0;
  }
}

.sorting-type__label {
  font-size: 20px;
  line-height: 1.4;
  padding-right: 6px;
}

.sorting-type__select {
  appearance: none;
  font-family: inherit;
  font-size: 17px;
  font-weight: inherit;
  padding: 5px 30px 5px 10px;
  outline: none;
  border-radius: 7px;
  background: url("../image/select-arrow.svg") no-repeat;
  background-size: 20px;
  background-position: 95% 62%;
}

.sorting-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  align-items: start;
  gap: 30px;
}
@media (min-width: 768px) {
  .sorting-list {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  }
}

.sorting-list__item--hidden {
  display: none;
}
@media (min-width: 1440px) {
  .sorting-list__item--marron {
    order: 1;
  }
  .sorting-list__item--mishka {
    order: 6;
  }
  .sorting-list__item--pink {
    order: 10;
  }
  .sorting-list__item--global {
    order: 9;
  }
  .sorting-list__item--kekstagram {
    order: 12;
  }
  .sorting-list__item--kasmir {
    order: 2;
  }
  .sorting-list__item--device {
    order: 8;
  }
  .sorting-list__item--cat-energy {
    order: 7;
  }
  .sorting-list__item--sedona {
    order: 5;
  }
  .sorting-list__item--offerrum {
    order: 11;
  }
  .sorting-list__item--spshop {
    order: 3;
  }
  .sorting-list__item--gussi {
    order: 4;
  }
}

.sorting-list__link {
  display: block;
}
.sorting-list__link:active .sorting-list__title {
  color: #e98b3f;
}

.sorting-list__image {
  display: block;
  margin: 0 auto;
  object-fit: cover;
  object-position: top;
  box-shadow: 10px 10px 30px #f1f1f1;
}
@media (min-width: 768px) {
  .sorting-list__image {
    width: 100%;
    min-width: 320px;
    min-height: 170px;
  }
}
@media (min-width: 1440px) {
  .sorting-list__image {
    min-width: 400px;
    min-height: 215px;
  }
}

.sorting-list__title {
  text-align: center;
  margin: 5px 0 0;
}
@media (min-width: 768px) {
  .sorting-list__title {
    margin: 20px auto 0;
    max-width: 263px;
  }
}
@media (min-width: 1440px) {
  .sorting-list__title {
    max-width: 100%;
  }
}

.footer {
  background-color: #000000;
  padding: 30px 0;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .footer__wrapper {
    flex-direction: row;
  }
}
@media (min-width: 1440px) {
  .footer__wrapper {
    max-width: 1320px;
    margin: 0 auto;
  }
}

.footer__address {
  font-style: normal;
  font-size: 18px;
}

.footer__link {
  color: #ffffff;
}

.social-list {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px 0;
  width: 100%;
  flex-wrap: wrap;
}
.social-list::before, .social-list::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
}
.social-list::before {
  top: 0;
}
.social-list::after {
  bottom: 0;
}
@media (min-width: 768px) {
  .social-list {
    max-width: 34%;
    padding: 0;
    margin: 0 auto;
  }
  .social-list::before, .social-list::after {
    content: none;
  }
}

.social-list__link .social-list__icon:active {
  fill: #e98b3f;
}

.info {
  font-size: 16.5px;
  text-indent: 1rem;
  background-color: #ffffff;
  padding-bottom: 50px;
}
@media (min-width: 1440px) {
  .info {
    font-size: 18px;
    text-indent: 0;
  }
}

.info__container {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (min-width: 1440px) {
  .info__container {
    max-width: 1320px;
    margin: 0 auto;
  }
}

.about-me__avatar {
  display: block;
  float: left;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  margin: 30px 13px;
  background: url("../image/decorative-images/avatar.jpg") no-repeat;
  background-size: contain;
  background-position: 50% 36%;
  box-sizing: border-box;
}
@media (min-resolution: 2dppx) {
  .about-me__avatar {
    background: url("../image/decorative-images/avatar@2x.jpg") no-repeat;
    background-size: contain;
  }
}
@media (min-width: 768px) {
  .about-me__avatar {
    width: 150px;
    height: 150px;
    margin: 10px 20px;
  }
}

.about-me__link {
  font-weight: 500;
}

.skills__title {
  font-size: 24px;
  font-weight: 400;
}

.skills__skills-list {
  list-style: disc;
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style-position: inside;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .skills__skills-list {
    flex-direction: row;
  }
}

.skills__text {
  margin-bottom: 0;
}

.experience-list {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (min-width: 1440px) {
  .experience-list {
    gap: 50px;
  }
}

.experience-list__item {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (min-width: 768px) {
  .experience-list__item {
    text-indent: 0;
  }
}
@media (min-width: 1440px) {
  .experience-list__item {
    gap: 20px;
  }
}

.experience-list__title {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-weight: 300;
}
@media (min-width: 768px) {
  .experience-list__title {
    flex-direction: row;
  }
}

.experience-list__title-first-date {
  display: flex;
  gap: 10px;
  margin: 0;
}

.experience-list__title-arrow {
  min-width: 20px;
  min-height: 20px;
  background: url("../image/arrow-right.svg") no-repeat;
  background-size: contain;
}

.experience-list__subtitle {
  margin: 0 0 10px;
}

.experience-list__tasks-list,
.experience-list__progress-list {
  list-style: disc;
  list-style-position: inside;
}

.experience-list__link {
  display: block;
  font-size: 16px;
  font-weight: 500;
  color: #474242;
  text-indent: 1rem;
}
@media (min-width: 768px) {
  .experience-list__link {
    text-indent: 0;
  }
}

.experience-list__container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 10px;
}
@media (min-width: 768px) {
  .experience-list__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media (min-width: 1440px) {
  .experience-list__container {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1440px) {
  .experience-list__tasks {
    grid-column: 2/-1;
  }
}